.todo-list-item {
	font-size: 1.5rem;
}

.todo-list-item button {
	width: 40px;
	height: 40px;
	margin: 5px;
}

.todo-list-item-label {
	margin-left: 0.5rem;
	line-height: 50px;
	cursor: pointer;
	user-select: none;
}

.todo-list-item.done .todo-list-item-label {
	text-decoration: line-through;
}

.todo-list-item.important .todo-list-item-label {
	font-weight: bold;
	color: blue;
}
